import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import(/* webpackChunkName: "home" */ '../views/Home')
  },
  {
    path: '/umzug/:uuid',
    name: 'move',
    component: () => import(/* webpackChunkName: "move" */ '../views/Move')
  }
]

const router = new VueRouter({
  routes
})

export default router
