const getters = {
  fullMove: state => {
    return state.move
  },
  uuid: state => {
    return state.move.uuid
  },
  addresses: state => {
    return state.move.addresses
  },
  billing: state => {
    return state.move.billing
  },
  persons: state => {
    return state.move.persons
  },
  currentStep: state => {
    return state.move.currentStep
  },
  informations: state => {
    return state.move.informations
  },
  checklist: state => {
    return state.move.checklist
  },
  serviceprovider: state => {
    return state.move.serviceprovider
  }
}

const mutations = {
  setFullMove: (state, payload) => {
    state.move = Object.assign(state.move, payload)
  },
  updateProperty: (state, { path, value }) => {
    recObjectByPath(state.move, path, value)
  },
  createPerson: (state) => {
    state.move.persons.further.push({
      salutation: '',
      title: '',
      firstname: '',
      lastname: '',
      birthdate: ''
    })
  },
  removePerson: (state, index) => {
    state.move.persons.further.splice(index, 1)
  },
  createAuthority: (state) => {
    state.move.informations.authorities.push({
      use: '',
      name: '',
      street: '',
      nr: '',
      additional: '',
      postcode: '',
      location: ''
    })
  },
  removeAuthority: (state, index) => {
    state.move.informations.authorities.splice(index, 1)
  },
  createInsurance: (state) => {
    state.move.informations.insurance.push({
      use: '',
      name: '',
      street: '',
      nr: '',
      additional: '',
      postcode: '',
      location: ''
    })
  },
  removeInsurance: (state, index) => {
    state.move.informations.insurance.splice(index, 1)
  },
  createEnergy: (state) => {
    state.move.informations.energy.push({
      use: '',
      name: '',
      street: '',
      nr: '',
      additional: '',
      postcode: '',
      location: ''
    })
  },
  removeEnergy: (state, index) => {
    state.move.informations.energy.splice(index, 1)
  },
  createBank: (state) => {
    state.move.informations.banks.push({
      use: '',
      name: '',
      customernr: '',
      street: '',
      nr: '',
      additional: '',
      postcode: '',
      location: ''
    })
  },
  removeBank: (state, index) => {
    state.move.informations.banks.splice(index, 1)
  },
  createFriend: (state) => {
    state.move.informations.friends.push({
      firstname: '',
      lastname: '',
      mail: ''
    })
  },
  removeFriend: (state, index) => {
    state.move.informations.friends.splice(index, 1)
  },
  nextStep: (state) => {
    state.move.currentStep++;
  },
  prevStep: (state) => {
    state.move.currentStep--;
  }
}

const actions = {
  loadFromLocalDB: async ({commit}, uuid) => {
    const db = fetchLocalDB(DB_NAME, dbName, dbLayout)

    const moves = await db[dbName].where('uuid').equals(uuid).toArray()
    const move = moves[0].object

    commit('setFullMove', move)
  },
  createNewMove: ({commit}) => {
    const moveStructure = Object.assign({}, state)
    moveStructure.move.uuid = uuidv4()

    commit('setFullMove', moveStructure.move)
  },
  createLocalMove: async (context) => {
    const db = fetchLocalDB(DB_NAME, dbName, dbLayout)
    const move = context.getters.fullMove

    await db[dbName].add({ uuid: move.uuid, object: move })
  },
  updateLocalMove: async (context) => {
    const db = fetchLocalDB(DB_NAME, dbName, dbLayout)
    const move = context.getters.fullMove

    await db[dbName].where('uuid').equals(move.uuid).modify({ object: move })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}